<template>
  <!--  -->
  <v-dialog v-model="dialog" :style="styles" :width="width" :max-width="maxWidth" persistent>
    <v-card>
      <!--  -->
      <v-card-title v-if="title" class="pa-0">
        <v-toolbar dark :color="titleColor">
          <v-toolbar-title class="headline font-weight-bold align-baseline">
            <v-icon v-if="titleIcon">{{ titleIcon }}</v-icon>
            {{ title }}
          </v-toolbar-title>
          <v-spacer />
          <v-toolbar-items>
            <!-- propsclosabletrue() -->
            <v-btn v-if="closable" icon @click="$emit('update:dialog', false)">
              <v-icon>mdi-close</v-icon>
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>
      </v-card-title>

      <!--  -->
      <v-card-text class="body-1 black--text" style="padding-top:20px">
        {{
        message
        }}
      </v-card-text>

      <!--  -->
      <v-divider />

      <!--  -->
      <v-card-actions>
        <v-spacer />
        <v-btn
          v-for="(button, index) in buttons"
          :key="index"
          :color="button.color"
          @click="button.function"
        >{{ button.text }}</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "Confirm",
  props: {
    dialog: Boolean,
    title: String,
    titleIcon: String,
    titleColor: String,
    message: String,
    buttons: Array,
    closable: Boolean,
    width: {
      type: [String, Number],
      default: undefined
    },
    maxWidth: {
      type: [String, Number],
      default: 500
    }
  },

  // maxWidthCSS
  computed: {
    // 
    styles() {
      return {
        "--maxWidth": this.maxWidth
      };
    }
  }
};
</script>

<style scoped>
/* VuetifyViewport */
/* .v-dialog__contentv-dialogCSS */

/* 600px */
@media screen and (min-width: 0px) and (max-width: 600px) {
  .v-dialog__content >>> .v-dialog {
    --maxWidth: calc(600px * 0.8);
    max-width: var(--maxWidth);
  }
}

/* 600px960px */
@media screen and (min-width: 600px) and (max-width: 960px) {
  .v-dialog__content >>> .v-dialog {
    --maxWidth: calc(960px * 0.8);
    max-width: var(--maxWidth);
  }
}
</style>
